@import url('https://fonts.googleapis.com/css2?family=EB+Garamond:ital,wght@0,600;1,700&family=Poppins:ital,wght@0,300;1,200;1,400&display=swap');

* {
  box-sizing: border-box;
  font-size: 18px;
  font-family: 'Poppins', sans-serif;
}

.container {
  width: 100%;
  background-color: #ebedf1;
}

.mini-container {
  width: 80%;
  margin: auto;
}

.rocket-card {
  display: flex;
  width: 100%;
  margin: 12px auto;
  border: 1px solid #d7dbe4;
}

.img-contain {
  flex-basis: 35%;
  height: 490px;
  padding: 28px;
  background-color: #d7dbe4;
}

.img-contain img {
  width: 100%;
  height: 100%;

  /* border-radius: 50%; */
}

.rocket-content {
  padding: 0 19px;
  flex-basis: 65%;
}

.rocket-content p {
  margin-bottom: 34px;
  font-size: 1rem;
}

.rocket-content h3 {
  font-size: 26px;
}

button {
  padding: 8px 16px;
  background-color: #42578b;
  color: #fff;
  border: none;
}

td button {
  background: none;
  border: 1px solid black;
  color: #877139;
}

button:hover {
  background-color: #877139;
}

button:active {
  background-color: #a8a08d;
}

.small {
  width: 10%;
  text-align: center;
}

table,
th,
td,
tr {
  border: 1px solid gray;
  padding: 18px 0;
  gap: 0;
}

td button:hover {
  background-color: #877139;
  color: #fff;
}

td span {
  background-color: #42578b;
  color: #fff;
  padding: 6px 12px;
}

.profile-container {
  display: flex;
  justify-content: space-around;
  width: 80%;
  margin: auto;
}

.profile-container table {
  border-collapse: collapse;
  padding: 10px 20px;
}

.profile-container table,
th,
td,
tr {
  border: 1px solid gray;
  padding: 18px 20px;
  gap: 0;
}
